*{
  margin: 0;
}
.app{
  background-color: #111;
}
.load{
  position: relative;
  width: 100%; min-height: 100vh;
  background-color: #111;
}
.innerLoad{
  position: absolute; left: calc(50% - 85px); top: calc(45% - 23px);
  width: 100%; height: 100%;
}
.loading{
  display: block;
  font-size: 4vw; font-weight: 500;
  color: #e5e5e5;
  margin-top: 20px;
  letter-spacing: 1px;
}